import { Link } from "react-router-dom"
import { BestCandidates, LastCandidates, LastJobs, MostViewedJobs } from "./PublicationsComponents"
export const DashWelcomeBanner = () => {
    return <>
        <h4 className='developpement-explain'>L'utilisateur candidat  vois ca 👇</h4>
        <div className="dash-welcomeBanner">
            <h2>Hello Pseudo utilisateur</h2>
            <section className="dwb-candidate">
                <Link to="">Mettre a jour mon profil</Link>
            </section>
        </div>
        <h4 className='developpement-explain'>L'utilisateur recruteur  vois ca 👇</h4>
        <div className="dash-welcomeBanner">
            <h2>Hello Pseudo Utilisateur</h2>
            <section className="dwb-employer">
                <Link to="">Postez une nouvelle offre</Link>
            </section>
        </div>
        <h4 className='developpement-explain'>L'utilisateur vip  vois ca 👇</h4>
        <div className="dash-welcomeBanner">
            <h2>Hello Nom VIP</h2>
            <section className="dwb-vip">
                <Link to="">Affaires</Link>
            </section>
        </div>
    </>
}

export const CandidatsList = () => {
    return <div className="dash-candidatsList">
        <h4 className='developpement-explain'>L'utilisateur recruteur  vois ca 👇</h4>
        <LastCandidates />
        <BestCandidates />
    </div>
}

export const JobOfferList = () => {
    return <div className="dash-jobOfferList">
        <h4 className='developpement-explain'>L'utilisateur candidat  vois ca 👇</h4>
        <LastJobs />
        <MostViewedJobs />
    </div>
}